	module.exports = {"data":{"challengeNode":{"title":"Adjust the Hover State of an Anchor Tag","description":["本挑战将要涉及到伪类。伪类是可以添加到选择器上的关键字，用来选择元素的指定状态。","比如，超链接可以使用<code>:hover</code>伪类选择器定义它的悬停状态样式。下面是悬停超链接时改变超链接颜色的 CSS：","<blockquote>a:hover {<br>&nbsp;&nbsp;color: red;<br>}</blockquote>","<hr>","代码编辑器里面已经有了一个 CSS 规则把所有的<code>a</code>标签定义成了黑色。添加一个规则，满足如下，当用户悬停<code>a</code>标签时，把<code>color</code>变成蓝色。"],"challengeType":0,"fields":{"slug":"/responsive-web-design/applied-visual-design/adjust-the-hover-state-of-an-anchor-tag","blockName":"Applied Visual Design","tests":[{"text":"超链接的<code>color</code>应该保持黑色，只添加<code>:hover</code> CSS 规则。","testString":"assert($('a').css('color') == 'rgb(0, 0, 0)', '超链接的<code>color</code>应该保持黑色，只添加<code>:hover</code> CSS 规则。');"},{"text":"悬停超链接时超链接<code>color</code>应该变成蓝色。","testString":"assert(code.match(/a:hover\\s*?{\\s*?color:\\s*?blue;\\s*?}/gi), '悬停超链接时超链接<code>color</code>应该变成蓝色。');"}]},"required":[],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<style>\n  a {\n    color: #000;\n  }\n  \n  \n  \n</style>\n<a href=\"http://freecatphotoapp.com/\" target=\"_blank\">猫咪相册 App</a>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/responsive-web-design/applied-visual-design/change-an-elements-relative-position","id":"587d781d367417b2b2512ac8"},"slug":"/responsive-web-design/applied-visual-design/adjust-the-hover-state-of-an-anchor-tag"}}

